import { Item, FiltersStorage } from '../../model';

export class Products {
  private items: Item[];
  constructor(items: Item[]) {
    this.items = items;
  }

  filterProductList(filters: FiltersStorage): Item[] {
    let filteredItems = this.items;
    if (filters.brand && filters.brand.length) {
      filteredItems = filteredItems.filter((item: Item) =>
        (filters.brand as string[]).includes(item.brand)
      );
    }

    if (filters.color && filters.color.length) {
      filteredItems = filteredItems.filter((item: Item) => {
        return (filters.color as string[]).filter((x) => item.color.includes(x))
          .length;
      });
    }

    if (filters.size && filters.size.length) {
      filteredItems = filteredItems.filter((item: Item) => {
        return (filters.size as string[])
          .map((n) => +n)
          .filter((x) => item.size.includes(x)).length;
      });
    }

    if (filters.popular) {
      filteredItems = filteredItems.filter((item: Item) => item.popular);
    }

    if (filters.newProduct) {
      filteredItems = filteredItems.filter((item: Item) => item.newProduct);
    }

    if (filters.count) {
      filteredItems = filteredItems.filter((item: Item) => {
        const min = filters.count!.min as number;
        const max = filters.count!.max as number;
        return item.count <= max && item.count >= min;
      });
    }

    if (filters.year) {
      filteredItems = filteredItems.filter((item: Item) => {
        const min = filters.year!.min as number;
        const max = filters.year!.max as number;
        return item.year <= max && item.year >= min;
      });
    }

    if (filters.search) {
      filteredItems = filteredItems.filter((item) => {
        const searchKey = (filters.search as string).toLowerCase();
        return item.name.toLowerCase().indexOf(searchKey) !== -1;
      });
    }

    const sorting = filters.sorting as string;
    filteredItems = this.sortProductList(filteredItems, sorting);

    return filteredItems;
  }

  sortProductList(items: Item[], sortingType: string): Item[] {
    let sortedItems = items;
    switch (sortingType) {
      case 'priceAsc':
        sortedItems = sortedItems.sort((a, b) => a.price - b.price);
        break;
      case 'priceDesc':
        sortedItems = sortedItems.sort((a, b) => b.price - a.price);
        break;
      case 'nameDesc':
        sortedItems = sortedItems.sort((a, b) => {
          const nameA = a.name.toLowerCase();
          const nameB = b.name.toLowerCase();
          if (nameA > nameB) {
            return -1;
          }
          if (nameA < nameB) {
            return 1;
          }
          return -1;
        });
        break;
      case 'yearAsc':
        sortedItems = sortedItems.sort((a, b) => a.year - b.year);
        break;
      case 'yearDesc':
        sortedItems = sortedItems.sort((a, b) => b.year - a.year);
        break;
      case 'nameAsc':
      default:
        sortedItems = sortedItems.sort((a, b) => {
          const nameA = a.name.toLowerCase();
          const nameB = b.name.toLowerCase();
          if (nameA < nameB) {
            return -1;
          }
          if (nameA > nameB) {
            return 1;
          }
          return 1;
        });
        break;
    }
    return sortedItems;
  }
}
