import { brands } from '../../../data';
import { arrayFilterClick } from '../filter-helper';
import './BrandFilter.css';
import { Dispatch, SetStateAction } from 'react';
import { FiltersStorage } from '../../../model';

function BrandFilter(props: {
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  return (
    <div>
      <div className="filter-brand">
        <h5>:</h5>
        <div className="brand-list"></div>
        {brands.map((item) => (
          <img
            className={`filter-brand__button ${
              props.stateValue.brand?.includes(item.name as string)
                ? `filter-brand__button-active`
                : ''
            }`}
            src={item.logoPath}
            title={item.name}
            alt={item.name}
            data-value={item.name}
            key={item.name}
            onClick={(e: React.MouseEvent<HTMLElement>) => {
              arrayFilterClick(
                e,
                'brand',
                props.stateValue,
                props.handleChange
              );
            }}
          ></img>
        ))}
      </div>
    </div>
  );
}
export default BrandFilter;
