import { Filters } from './../../model';
import { Dispatch, SetStateAction } from 'react';
import { FiltersStorage } from '../../model';

export function getInitialFilters(): FiltersStorage {
  let filters: FiltersStorage = {};
  filters.brand = JSON.parse(localStorage.getItem('brand') as string) ?? [];
  filters.color = JSON.parse(localStorage.getItem('color') as string) ?? [];
  filters.size = JSON.parse(localStorage.getItem('size') as string) ?? [];
  filters.popular = !!localStorage.getItem('popular') ?? '';
  filters.newProduct = !!localStorage.getItem('newProduct') ?? '';
  filters.count = JSON.parse(localStorage.getItem('count') as string) ?? [
    { min: 2, max: 8 },
  ];
  filters.year = JSON.parse(localStorage.getItem('year') as string) ?? [
    { min: 2015, max: 2022 },
  ];
  filters.search = (localStorage.getItem('search') as string) ?? '';
  filters.sorting = (localStorage.getItem('sorting') as string) ?? '';
  return filters;
}

export function arrayFilterClick(
  e: React.MouseEvent<HTMLElement>,
  field: 'brand' | 'color' | 'size',
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  const el = e.target as HTMLElement;
  el.classList.toggle(`filter-${field}__button-active`);
  const filterKey = el.dataset.value as string;
  const storedValues = window.localStorage.getItem(field);
  let filterKeys =
    typeof storedValues === 'string' ? JSON.parse(storedValues) : [];
  let newFilterKeys: string[] = [];
  newFilterKeys = !filterKeys.includes(filterKey)
    ? [...filterKeys, filterKey]
    : filterKeys.filter((el: string) => el !== filterKey);
  window.localStorage.setItem(field, JSON.stringify(newFilterKeys));
  const newState: FiltersStorage = { ...stateValue };
  newState[field] = newFilterKeys;
  handleChange(newState);
}

export function boolFilterClick(
  e: React.MouseEvent<HTMLElement>,
  field: 'popular' | 'newProduct',
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  const newState: FiltersStorage = { ...stateValue };
  const el = e.target as HTMLElement;
  el.classList.toggle(`filter-${field}__button-active`);
  const storedValue = window.localStorage.getItem(field);
  if (storedValue) {
    window.localStorage.removeItem(field);
    newState[field] = false;
  } else {
    window.localStorage.setItem(field, '1');
    newState[field] = true;
  }
  handleChange(newState);
}

export function searchEnter(
  e: React.KeyboardEvent<HTMLElement>,
  field: string,
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  if ((e as React.KeyboardEvent).keyCode !== 13) {
    return;
  }
  const filterKey = (<HTMLInputElement>(
    document.querySelector('[name="search"]')
  )).value as string;
  window.localStorage.setItem(field, filterKey);
  const newState: FiltersStorage = { ...stateValue };
  newState.search = filterKey;
  handleChange(newState);
}

export function searchClick(
  e: React.MouseEvent<HTMLElement> | React.KeyboardEvent<HTMLElement>,
  field: string,
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  const filterKey = (<HTMLInputElement>(
    document.querySelector('[name="search"]')
  )).value as string;
  window.localStorage.setItem(field, filterKey);
  const newState: FiltersStorage = { ...stateValue };
  newState.search = filterKey;
  handleChange(newState);
}

export function clearSearchQuery(
  e: React.FormEvent<HTMLInputElement>,
  field: string,
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  const filterKey = (e.target as HTMLSelectElement).value as string;
  if (filterKey === '') {
    window.localStorage.removeItem(field);
    const newState: FiltersStorage = { ...stateValue };
    newState.search = '';
    handleChange(newState);
  }
}

export function resetFiltersClick(
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  Object.keys(Filters).forEach((key) => {
    if (key !== 'sorting') {
      window.localStorage.removeItem(key);
      document
        .querySelectorAll(`.filter-${key}__button-active`)
        .forEach((el) => {
          el.classList.remove(`filter-${key}__button-active`);
        });
    }
  });
  let newState: FiltersStorage = {};
  stateValue.sorting ? (newState.sorting = stateValue.sorting) : '';
  stateValue.count = { min: 2, max: 8 };
  stateValue.year = { min: 2015, max: 2022 };
  handleChange(newState);
}

export function sortingClickHandler(
  e: React.FormEvent<HTMLSelectElement>,
  field: 'sorting',
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  const filterKey = (e.target as HTMLSelectElement).value as string;
  window.localStorage.setItem(field, filterKey);
  const newState: FiltersStorage = { ...stateValue };
  newState[field] = filterKey;
  handleChange(newState);
}

export function onSlideCount(
  data: string[],
  field: 'year' | 'count',
  stateValue: FiltersStorage,
  handleChange: Dispatch<SetStateAction<FiltersStorage>>
) {
  const minValue = document.querySelector(`#range-${field}-min`) as HTMLElement;
  const maxValue = document.querySelector(`#range-${field}-max`) as HTMLElement;
  minValue.innerText = `${+data[0]}`;
  maxValue.innerText = `${+data[1]}`;
  const newValue = {
    min: +data[0],
    max: +data[1],
  };
  window.localStorage.setItem(field, JSON.stringify(newValue));
  const newState: FiltersStorage = { ...stateValue };
  newState[field] = newValue;
  handleChange(newState);
}
