import { boolFilterClick } from '../filter-helper';
import { Dispatch, SetStateAction } from 'react';
import { FiltersStorage } from '../../../model';
import './BoolFilter.css';

function BoolFilter(props: {
  field: 'popular' | 'newProduct';
  name: string;
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  return (
    <div
      className={`filter-${props.field}__button ${
        props.stateValue[props.field]
          ? `filter-${props.field}__button-active`
          : ''
      }`}
      title={props.name}
      onClick={(e: React.MouseEvent<HTMLElement>) => {
        boolFilterClick(e, props.field, props.stateValue, props.handleChange);
      }}
    >
      {props.name}
    </div>
  );
}
export default BoolFilter;
