import { resetFiltersClick } from '../filter-helper';
import { Dispatch, SetStateAction } from 'react';
import { FiltersStorage } from '../../../model';
import './ResetButton.css';

function ResetButton(props: {
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  return (
    <div className="reset-button-container">
      <div
        className="reset-filters__button"
        title=" "
        onClick={(e: React.MouseEvent<HTMLInputElement>) => {
          resetFiltersClick(props.stateValue, props.handleChange);
        }}
      >
         
      </div>
    </div>
  );
}
export default ResetButton;
