import './Main.css';
import Product from './Product';
import { Products } from './Products';
import { getInitialFilters } from './../filters/filter-helper';
import { items } from '../../data';
import { useState } from 'react';
import FiltersContainer from '../filters/FiltersContainer';

function Main() {
  const filters = getInitialFilters();
  const [itemFilters, setItemFilters] = useState(filters);

  const products = new Products(items);
  const product_list = products.filterProductList(itemFilters);
  return (
    <main>
      <FiltersContainer
        stateValue={itemFilters}
        handleChange={setItemFilters}
      ></FiltersContainer>
      <div className="products-list">
        {product_list.map((item) => (
          <Product item={item} key={item.id}></Product>
        ))}
      </div>
    </main>
  );
}
export default Main;
