import { sortingClickHandler } from '../filter-helper';
import React, { Dispatch, SetStateAction } from 'react';
import { FiltersStorage, SortingType } from '../../../model';
import './Sorting.css';

function Sorting(props: {
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  const storedValue = window.localStorage.getItem('sorting') as string;
  let sortingTypes = [];
  for (let i = 0; i < Object.keys(SortingType).length; i++) {
    sortingTypes.push({
      key: Object.keys(SortingType)[i],
      value: Object.values(SortingType)[i],
    });
  }
  return (
    <div className="sorting-container">
      <select
        className="sorting-dropdown"
        name="sorting"
        onChange={(e: React.FormEvent<HTMLSelectElement>) => {
          sortingClickHandler(
            e,
            'sorting',
            props.stateValue,
            props.handleChange
          );
        }}
        defaultValue={storedValue}
      >
        {sortingTypes.map((item) => (
          <option
            className="sorting-option"
            data-value={item.key}
            value={item.key}
            key={item.key}
          >
            {item.value}
          </option>
        ))}
      </select>
    </div>
  );
}
export default Sorting;
