import Nouislider from 'nouislider-react';
import { onSlideCount } from './../filter-helper';
import './CountRangeFilter.css';
import './nouislider.css';
import React from 'react';
import { Dispatch, SetStateAction } from 'react';
import { FiltersStorage, Ranges } from '../../../model';

function CountRangeFilter(props: {
  field: 'year' | 'count';
  min: string;
  max: string;
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  const rangeState: Ranges = {
    year: {
      min: +(props.stateValue.year?.min ?? 2015),
      max: +(props.stateValue.year?.max ?? 2022),
    },
    count: {
      min: +(props.stateValue.count?.min ?? 1),
      max: +(props.stateValue.count?.max ?? 10),
    },
  };
  return (
    <React.Fragment>
      <div className={`range-${props.field}-container`}>
        <div
          id={`range-${props.field}-min`}
          className={`range-${props.field}-value`}
        >
          {rangeState[props.field].min}
        </div>
        <Nouislider
          id={`range-${props.field}`}
          range={{ min: +props.min, max: +props.max }}
          start={[rangeState[props.field].min, rangeState[props.field].max]}
          step={1}
          connect
          className="range-slider"
          onSlide={(data) => {
            onSlideCount(
              data,
              props.field,
              props.stateValue,
              props.handleChange
            );
          }}
        />
        <div
          id={`range-${props.field}-max`}
          className={`range-${props.field}-value`}
        >
          {rangeState[props.field].max}
        </div>
      </div>
    </React.Fragment>
  );
}

export default CountRangeFilter;
