import React from 'react';
import { searchClick, clearSearchQuery, searchEnter } from '../filter-helper';
import { Dispatch, SetStateAction } from 'react';
import { FiltersStorage } from '../../../model';
import './Search.css';

function Search(props: {
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  const storedValue = window.localStorage.getItem('search') as string;

  return (
    <div className="seacrh-container">
      <div className="search-input">
        <input
          type="search"
          autoComplete="off"
          name="search"
          defaultValue={storedValue}
          placeholder="  "
          onKeyDown={(e: React.KeyboardEvent<HTMLInputElement>) => {
            searchEnter(e, 'search', props.stateValue, props.handleChange);
          }}
          onChange={(e: React.FormEvent<HTMLInputElement>) => {
            clearSearchQuery(e, 'search', props.stateValue, props.handleChange);
          }}
        ></input>
        <div
          className="search-icon"
          onClick={(e: React.MouseEvent<HTMLInputElement>) => {
            searchClick(e, 'search', props.stateValue, props.handleChange);
          }}
        >
          <svg
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="64"
            height="64"
            viewBox="0 0 64 64"
          >
            <title>search</title>
            <path
              fill="#5fe2b6"
              d="M62.016 54.462l-15.16-12.893c-1.567-1.41-3.243-2.058-4.597-1.995 3.578-4.192 5.74-9.63 5.74-15.573 0-13.255-10.745-24-24-24s-24 10.745-24 24 10.745 24 24 24c5.944 0 11.382-2.162 15.574-5.74-0.063 1.354 0.585 3.030 1.995 4.597l12.893 15.16c2.208 2.453 5.814 2.66 8.014 0.46s1.993-5.806-0.46-8.014zM24 40c-8.836 0-16-7.163-16-16s7.163-16 16-16 16 7.163 16 16-7.163 16-16 16z"
            ></path>
          </svg>
        </div>
      </div>
    </div>
  );
}
export default Search;
