import { sizes } from '../../../data';
import { arrayFilterClick } from '../filter-helper';
import './SizeFilter.css';
import { Dispatch, SetStateAction, useEffect } from 'react';
import { FiltersStorage } from '../../../model';

function SizeFilter(props: {
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  return (
    <div className="filter-size">
      <div className="size-list">
        {sizes.map((item: number) => (
          <div
            className={`filter-size__button ${
              props.stateValue.size?.includes(item.toString())
                ? `filter-size__button-active`
                : ''
            }`}
            title={`size: ${item}`}
            data-value={item}
            key={item}
            onClick={(e: React.MouseEvent<HTMLElement>) => {
              arrayFilterClick(e, 'size', props.stateValue, props.handleChange);
            }}
          >
            {item}
          </div>
        ))}
      </div>
    </div>
  );
}
export default SizeFilter;
