import { Color } from '../../../model/enum/color.enum';
import { arrayFilterClick } from '../filter-helper';
import './ColorFilter.css';
import { Dispatch, SetStateAction, useEffect } from 'react';
import { FiltersStorage } from '../../../model';

function ColorFilter(props: {
  stateValue: FiltersStorage;
  handleChange: Dispatch<SetStateAction<FiltersStorage>>;
}) {
  let colors = [];
  for (let i = 0; i < Object.keys(Color).length; i++) {
    colors.push({ key: Object.keys(Color)[i], value: Object.values(Color)[i] });
  }
  return (
    <div className="filter-color">
      <h5>:</h5>
      <div className="color-list">
        {colors.map((item: { key: string; value: string }) => (
          <div
            className={`filter-color__button 
          filter-color__button-${item.value}
          ${
            props.stateValue.color?.includes(item.key as string)
              ? `filter-color__button-active`
              : ''
          }`}
            title={item.key}
            data-value={item.key}
            key={item.key}
            onClick={(e: React.MouseEvent<HTMLElement>) => {
              arrayFilterClick(
                e,
                'color',
                props.stateValue,
                props.handleChange
              );
            }}
          ></div>
        ))}
      </div>
    </div>
  );
}
export default ColorFilter;
