import { useState } from 'react';
import { syncBucket } from './items-helper';
import { Item } from '../../model';
import './Product.css';

function Product(props: { item: Item }) {
  const [itemCountStorage, setItemCountStorage] = useState(props.item.count);
  const [itemCount, setItemCount] = useState(0);

  const onClickAddButton = () => {
    if (itemCountStorage > 0) {
      let newCountStorage = itemCountStorage - 1;
      setItemCountStorage(newCountStorage);
      let newCount = itemCount + 1;
      setItemCount(newCount);
      syncBucket(props.item.id, newCount);
    }
  };

  const product = props.item;
  return (
    <div className="product-card">
      <img
        src={product.imagePath}
        alt={product.name}
        data-value={product.id}
        title={product.name}
      ></img>
      <div className="product-card__name">{product.name}</div>
      <div className="product-card__count">
         : {itemCountStorage}
        <button onClick={onClickAddButton} className="add-button">
          {itemCount}
        </button>
      </div>
      <div className="product-card__size">: {product.size}</div>
      <div className="product-card__brand">
        {product.brand} : {product.year}
      </div>
    </div>
  );
}
export default Product;
